<template>
	<v-layout row>
    <v-flex xs12 sm6 offset-sm3>
      <v-toolbar flat class="purple" dark>
        <v-toolbar-title>New ToDo</v-toolbar-title>
      </v-toolbar>
      <v-card class="primary white">
        <v-card-text>
          <v-layout row>
            <v-flex xs12 sm6>
              <v-text-field
                label="Name"
                v-model="name"
                prepend-icon="create"
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6>
              <v-menu
                lazy
                :close-on-content-click="false"
                v-model="menu"
                transition="scale-transition"
                offset-y
                full-width
                :nudge-left="40"
                max-width="290px"
              >
                <v-text-field
                  slot="activator"
                  label="Date when ToDo is due"
                  v-model="dueDate"
                  prepend-icon="event"
                  readonly
                ></v-text-field>
                <v-date-picker
                  v-model="dueDate"
                  no-title
                  scrollable
                  actions
                >
                  <template scope=" { save, cancel }">
                    <v-card-actions>
                      <v-btn flat primary @click.native="cancel()">Cancel</v-btn>
                      <v-btn flat primary @click.native="save()">Save</v-btn>
                    </v-card-actions>
                  </template>
                </v-date-picker>
              </v-menu>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6>
              <v-menu
                lazy
                :close-on-content-click="false"
                v-model="menuTime"
                transition="scale-transition"
                offset-y
                full-width
                :nudge-left="40"
                max-width="290px"
              >
                <v-text-field
                  slot="activator"
                  label="Time when ToDo is due"
                  v-model="dueTime"
                  prepend-icon="alarm"
                  readonly
                ></v-text-field>
                <v-time-picker
                  v-model="dueTime"
                  no-title
                  scrollable
                  actions
                >
                  <template scope=" { save, cancel }">
                    <v-card-actions>
                      <v-btn flat primary @click.native="cancel()">Cancel</v-btn>
                      <v-btn flat primary @click.native="save()">Save</v-btn>
                    </v-card-actions>
                  </template>
                </v-time-picker>
              </v-menu>
            </v-flex>
          </v-layout>
        </v-card-text>
        <v-card-actions>
          <v-btn class="blue--text" @click.native.stop="createToDo" primary flat>Create</v-btn>
          <v-btn class="red--text" @click.native.stop="goBack" flat>Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import database from "../database"
window.database = database;
export default {
	name: 'createToDo',
  data() {
    return {
      dueDate: null,
      dueTime: null,
      name: null,
      menuTime: false,
      menu: false
    }
  },
  methods: {
    createToDo: function () {
      console.log(this.name, this.dueDate, this.dueTime);
      database.addToDo(this.name, this.dueDate, this.dueTime, (err, id) => console.log(err, id))
      this.$router.push('/')
    },
    goBack: function () {
      this.$router.go(-1)
    }
  }
}
</script>


